<template>
    <div class ="clientPage">
    
      <input v-model="description" type="text" placeholder="Enter Description" />
      <button @click="updateDescription">Submit</button>
      <button @click="correctDescription">Correct</button>
      <p>{{ message }}</p>
    </div>
</template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        photoInfo:[],
        description: '', // Description to be updated
        id: '1', // ID of the photo to be updated
        message: '', // Feedback message
      };
    },

    methods: {

      async updateDescription() {
      await  axios.patch('http://localhost:3000/photos/'+this.id, {
    
        newDescription: this.description,
        ischeacked: "true",
     
          })
          .then((response) => {
            this.message = response.data.message;
          })
          .catch((error) => {
            this.message = error.response.data.message;
          });
      },
      async correctDescription() {
      await  axios.patch('http://localhost:3000/photos/'+this.id, {
        isOk: "true",
        ischeacked: "true",
     
          })
          .then((response) => {
            this.message = response.data.message;
          })
          .catch((error) => {
            this.message = error.response.data.message;
          });
      }


    }
}
   
  </script>
  